.Container {
    width: 100%;
    height: 100vh;
    display: flex;
    position: relative;
    overflow: hidden;
    @media screen and (max-width:380px) {
        display: none;
    }

    .arrowLeft {
        left: 10px;
    }
    .arrowRight {
        right: 10px;
    }
    .arrowLeft,
    .arrowRight {
        width: 50px;
        height: 50px;
        background-color: #fff7f7;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        cursor: pointer;
        opacity: 0.5;
        z-index: 2;
    }

    .wrapper {
        height: 100%;
        display: flex;
        transition: all 1.5s ease;
        .slide {
            width: 100vw;
            height: 100vh;
            display: flex;
            align-items: center;

            .imgContainer {
                height: 100%;
                flex: 1;

                img {
                    height: 80%;
                }
            }

            .infoContainer {
                flex: 1;
                padding: 50px;

                .title {
                    font-size: 70px;
                }

                .desc {
                    margin: 50px 0px;
                    font-size: 20px;
                    font-weight: 500;
                    letter-spacing: 3px;
                }

                button {
                    padding: 10px;
                    font-size: 20px;
                    background-color: transparent;
                    cursor: pointer;
                }
            }
        }
    }
}