.Container {
    height: 60vh;
    background-color: #fcf5f5;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .title {
        font-size: 70px;
        margin-bottom: 20px;
    }

    .desc {
        font-size: 24px;
        font-weight: 300;
        margin-bottom: 20px;
        @media screen and (max-width:380px) {
            text-align: center;
        }
    }

    .inputContainer {
        width: 50%;
        height: 40px;
        background-color: white;
        display: flex;
        justify-content: space-between;
        border: 1px solid lightgray;
        @media screen and (max-width:380px) {
            width: 80%;
        }
        input {
            border: none;
            outline: none;
            flex: 8;
            padding-left: 20px;
        }

        button {
            flex: 1;
            border: none;
            background-color: teal;
            color: white;
            cursor: pointer;
        }
    }
}